  componentDidMount() {
    EventManager.addHandler('menuplayer', (value) => {
      if (value.type === 'show') {
        this.setState({ show: true });
      }

      if (value.type === 'hide') {
        this.setState({ show: false });
      } else return;
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('menuplayer');
  }

  closeMenu() {
    console.log('click');
    mp.trigger('client:menuplayer:closeMenu'); // eslint-disable-line
  }

  unlockAchievement(id) {
    if (parseInt(this.state.content.progress.achievements[id].current) < parseInt(this.state.content.progress.achievements[id].total)) {
      return;
    }
    this.setState((prev) => ({
      content: {
        ...prev.content,
        progress: {
          ...prev.content.progress,
          achievements: {
            ...prev.content.progress.achievements,
            [id]: {
              ...prev.content.progress.achievements[id],
              unlocked: true,
            },
          },
        },
      },
    }));
    mp.trigger('client:menuplayer:unlockachievement', id); // eslint-disable-line
  }

