import React from 'react';
import EventManager from '../../EventManager';

import { toast, cssTransition } from 'react-toastify';
import Body from './components/Body';

import './css/notifi.css';

const animation = {
  fadeUp: cssTransition({
    enter: 'animate__animated animate__fadeInUp',
    exit: 'animate__animated animate__fadeOutDown',
    // appendPosition: true,
  }),
};

class Notifi extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    EventManager.addHandler('notifi', (value) => {
      if (value.type === 'show') {
        this.showNotification(value.data, 'notifi_bottom_center', 1000, value.autoClose, 'fadeUp');
      }
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('notifi');
  }

  showNotification(data, containerId, delay, autoClose, animationString) {
    toast(<Body data={data} />, {
      transition: animation[animationString], //  (  -> anime.css)
      containerId: containerId, // 
      delay: delay, //   
      autoClose: autoClose, //   
    });
  }
  //  
  // clickcheck() {
  //   this.showNotification({ type: 1, notifi: 'accept', text: '1,  -   ' }, 'notifi_bottom_center', 1000, 4000, 'fadeUp');
  //   this.showNotification({ type: 1, notifi: 'accept', text: '2 !' }, 'notifi_bottom_center', 1000, 2000, 'fadeUp');
  //   this.showNotification({ type: 1, notifi: 'error', text: '3    ' }, 'notifi_bottom_center', 1000, 2000, 'fadeUp');
  //   this.showNotification({ type: 1, notifi: 'accept', text: '4,  -   ' }, 'notifi_bottom_center', 1000, 4000, 'fadeUp');
  //   this.showNotification({ type: 1, notifi: 'accept', text: '5 !' }, 'notifi_bottom_center', 1000, 2000, 'fadeUp');
  //   this.showNotification({ type: 1, notifi: 'error', text: '6    ' }, 'notifi_bottom_center', 1000, 2000, 'fadeUp');
  // }

  render() {
    return <React.Fragment>{/* <input type='button' className='test_button' onClick={() => this.clickcheck()} /> */}</React.Fragment>;
  }
}

export default Notifi;
